export default {
    name: "operation",
    data(){
        return{
            list: [
                { prc:require("@/assets/images/zht.png"),txt:"状态",to:"/operation/state" },
                { prc:require("@/assets/images/xiaban.png"),txt:"下班",to:"/operation/offDuty" },
                { prc:require("@/assets/images/tixing.png"),txt:"提醒",to:"/operation/set" },
                { prc:require("@/assets/images/rili.png"),txt:"日历",to:"/operation/calendar" }
            ],
            scroll: null
        }
    },
    methods:{
        onScroll (pos) {
            this.scroll = pos.left
        },
    },
    watch:{
        scroll(){
            if(this.scroll < -50){
                this.$router.push('/home')
            }
        }
    }
}



